.dropContainer {
  width: 70vw;
  height: 10vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 5rem;
  padding: 0 0.5rem;
}

.dropdown {
  appearance: none;

  width: 80%;
  height: 65px;
  border: none;
  border-radius: 12px;
  padding: 0 20px 0;
  background-color: #2f2f2f;
  outline: none;
  font-size: 24px;
  color: white;
  font-family: var(--font-mono);
}
.dropdown > option {
  outline: none;
  font-size: 16px;
}
.dropBtn {
  width: 20%;
  margin: 10px 30px 10px;
  height: 65px;
  font-size: 16px;
  background-color: #00b2ff;
  border-radius: 12px;
  border: none;
  font-family: var(--font-mono-);
}

.dropBtn:hover {
  transform: scale(1.1);
  cursor: pointer;
}

@media only screen and (max-width: 1000px) {
  .dropContainer {
    width: 70vw;
  }
}
@media only screen and (max-width: 650px) {
  .dropContainer {
    width: 90vw;
    flex-direction: column;
    height: 5rem;
    align-items: flex-end;
    margin-bottom: 1rem;
  }
  .dropdown {
    width: 100%;
  }
  .dropBtn {
    width: 20vw;
    font-size: 16px;
    margin-top: 10px;
  }
}

/* -------------------issue section ---------------------- */

.issueContainer {
  width: 100%;
  min-height: fit-content;
  border-radius: 10px;
  display: flex;
  padding: 2vh 0;
  justify-content: space-evenly;
  align-items: center;
  background-color: var(--grey);
  margin-bottom: 1rem;
}

.svg {
  width: 2.5rem;
  background-color: var(--grey);
}

.issueContainer > p {
  font-size: 18px;
  max-width: 60%;
  background-color: var(--grey);
}
.issueContainer > h3 {
  background-color: var(--grey);
}
.issueContainer > span {
  padding: 4px 4px;
  background-color: var(--yellow);
  border-radius: 10px;
  color: black;
  font-size: 14px;
  min-width: 6.5rem;
}

.issueContainer > button {
  font-size: 17px;
  padding: 2px 8px;
  background-color: var(--lightBlue);
  border: none;
  border-radius: 5px;
}
.issueContainer > button:hover {
  background-color: var(--lightestBlue);
}
.mobile-only {
  display: none;
  width: 90%;
  min-height: 1rem;
}

@media only screen and (max-width: 750px) {
  .desktop-only {
    display: none;
  }
  .issueContainer {
    position: relative;
    min-height: 6.5rem;
    align-items: flex-start;
    padding: 1rem 0;
  }
  .mobile-only {
    margin-top: 15px;
    display: flex;
    justify-content: center;
    position: absolute;
    bottom: 2px;
    left: 0;
    background-color: var(--grey);
    border-radius: 10px;
  }
  .mobile-only > p {
    background-color: var(--grey);
    font-family: var(--font-sans-);
  }
  .issueContainer > p {
    max-width: 100%;
    margin-top: 60px;
    padding-bottom: 10px;
    margin-left: 10px;
  }
}

/* --------------------preview Info section ----------- */
.previewInfo {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.previewInfo > button {
  font-size: 20px;
  padding: 5px 8px;
  margin-top: 1rem;
  background-color: var(--lightBlue);
  border: none;
  border-radius: 7px;
}
.previewInfo > button:hover {
  background-color: var(--lightestBlue);
}

.previewCon {
  width: 60vw;
  height: 70vh;
  border: 2px solid white;
  background-color: var(--grey);
  display: flex;
  justify-content: center;
  align-items: center;
}
.previewCon > h1 {
  background-color: var(--grey);
}
@media only screen and (max-width: 650px) {
  .previewCon {
    width: 90%;
  }
}
/* width */
::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey;
  border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: blue;
  border-radius: 10px;
}

::-webkit-resizer {
  size: 1px;
}

.repoContainer {
  margin: 5vh;
  border-radius: 2rem;
  width: 70vw;
  border: #0e42f1 1px solid;
  overflow: auto;
  padding: 25px;
}

.mdPar {
  width: 50vw;
  object-fit: contain;
  background: none;
}
.mdPar p {
  font-size: 1.3rem;
  width: 50vw;
  background: none;
  margin: 2vh 0;
}
.mdPar p a {
  font-size: 1.3rem;
  width: 40vw;
  margin: 2vh 0;
  background: none;
  word-wrap: break-word;
}

.repoContainer h2 {
  margin: 1vh 0 7.5vh;
  text-align: center;
}

.repoContainer h4 {
  text-align: center;
}
